.home {
  padding: 10px; }
  .home .info {
    position: relative; }
    .home .info .ripple-loader {
      position: absolute;
      top: 12px;
      left: 770px;
      cursor: pointer; }
    .home .info .ripple-loader div {
      position: absolute;
      border: 4px solid #b2fcff;
      border-radius: 50%;
      -webkit-animation: ripple-loader 1.2s ease-out infinite;
              animation: ripple-loader 1.2s ease-out infinite; }
    .home .info .ripple-loader div:nth-child(2) {
      -webkit-animation-delay: -0.5s;
              animation-delay: -0.5s; }

@-webkit-keyframes ripple-loader {
  0% {
    top: 21px;
    left: 21px;
    width: 0;
    height: 0;
    opacity: 1; }
  100% {
    top: 0;
    left: 0;
    width: 42px;
    height: 42px;
    opacity: 0; } }

@keyframes ripple-loader {
  0% {
    top: 21px;
    left: 21px;
    width: 0;
    height: 0;
    opacity: 1; }
  100% {
    top: 0;
    left: 0;
    width: 42px;
    height: 42px;
    opacity: 0; } }
    .home .info .avatar {
      float: right;
      margin-right: 59px; }
      .home .info .avatar img {
        width: 90px;
        height: 88px;
        border-radius: 12px;
        box-shadow: 0 3px 13px rgba(0, 0, 0, 0.15); }
      .home .info .avatar:hover {
        -webkit-animation: jiggle 1.4s ease-in-out;
                animation: jiggle 1.4s ease-in-out; }

@-webkit-keyframes jiggle {
  48% {
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1); }
  50% {
    -webkit-transform: scale(1.2, 0.8);
            transform: scale(1.2, 0.8); }
  56% {
    -webkit-transform: scale(0.8, 1.2) translate(0, -5px);
            transform: scale(0.8, 1.2) translate(0, -5px); }
  59% {
    -webkit-transform: scale(1, 1) translate(0, -3px);
            transform: scale(1, 1) translate(0, -3px); } }

@keyframes jiggle {
  48% {
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1); }
  50% {
    -webkit-transform: scale(1.2, 0.8);
            transform: scale(1.2, 0.8); }
  56% {
    -webkit-transform: scale(0.8, 1.2) translate(0, -5px);
            transform: scale(0.8, 1.2) translate(0, -5px); }
  59% {
    -webkit-transform: scale(1, 1) translate(0, -3px);
            transform: scale(1, 1) translate(0, -3px); } }
